/* modules */
.visible-xxs {
  display: none !important; }

.content {
  padding-top: 45px;
  padding-bottom: 55px;
  &__wrapper {
    background: $color-white;
    border-radius: 4px;
    padding: 24px 28px 28px 28px;
    min-height: 500px;
    @include clearfix; }
  &__title {
    font-size: rem(15);
    font-family: $font-roboto-bold;
    border-bottom: 1px solid #e5e5e5;
    margin-bottom: 17px;
    line-height: 24px; }
  &__date {
    color: $color-white;
    background: $color-1;
    text-align: center;
    height: 18px;
    line-height: 18px;
    width: 56px;
    font-size: rem(11);
    font-family: $font-roboto-light;
    margin-top: 3px; }
  &__table {
    margin-bottom: 10px;
    td {
      padding: 0 15px 15px 0;
      &:last-child {
        padding-right: 0; } }
    p {
      &:last-child {
        margin-bottom: 0; } } } }

.main-slider {
  box-shadow: 2px 2px 10px rgba($color-2,.1);
  margin-bottom: 70px;
  .slick {
    &-dots {
      height: 12px;
      bottom: 14px;
      li {
        vertical-align: 10px;
        height: 11px;
        margin: 0 2px;
        width: 11px;
        button {
          width: 12px;
          height: 12px;
          background: $color-white;
          border-radius: 50%;
          box-shadow: 0 0 0 0 $color-white inset;
          font-size: 0;
          border: 1px solid #dbdbde;
          &::before {
            display: none; } }
        &.slick-active {
          button {
            background: $color-1;
            border-color: $color-1;
            box-shadow: 0 0 0 2px $color-white inset; } } } } } }

.main-catalog {
  padding-top: 10px;
  &__block {
    background: $color-white;
    overflow: hidden;
    border-radius: 3px;
    height: 240px;
    display: block;
    margin-bottom: 30px;
    box-shadow: 2px 2px 10px rgba($color-2,.1);
    transition: all 0.35s;
    text-decoration: none !important;
    position: relative;
    color: $color-2;
    @include clearfix;
    &:hover {
      color: $color-2;
      box-shadow: 0px 0px 0px rgba($color-2,.1);
      &>svg {
        .path--color {
          fill: $color-1; } } }
    &>svg {
      display: block;
      width: 233px;
      height: 248px;
      position: absolute;
      top: -1px;
      right: -51px;
      z-index: 0;
      .path--color {
        transition: all 0.35s;
        fill: #dbdbde; } }
    &-img {
      position: absolute;
      right: 0;
      bottom: 0;
      z-index: 5; }
    &-title {
      color: $color-2;
      font-family: $font-baron;
      position: absolute;
      left: 35px;
      top: 34px;
      font-size: rem(26);
      line-height: 30px;
      &::before {
        position: absolute;
        width: 24px;
        height: 18px;
        top: 6px;
        background: $color-1;
        color: $color-white;
        content: "\f3d1";
        font-family: $font-ionicons;
        font-size: rem(9);
        text-align: center;
        line-height: 20px;
        left: -35px; } }
    &-list {
      position: absolute;
      font-size: rem(15);
      left: 33px;
      top: 137px;
      &>span {
        display: block;
        position: relative;
        padding-left: 13px;
        line-height: 1;
        margin-bottom: 15px;
        &:last-child {
          margin-bottom: 0; }
        &::before {
          position: absolute;
          content: "\f125";
          font-family: $font-ionicons;
          left: 0;
          top: 2px;
          line-height: 1;
          color: $color-1;
          font-size: rem(10); }
        a {
          color: $primary-color;
          &:hover, &:focus {
            text-decoration: none;
            color: $color-red; } } } } } }

.tab-content > .tab-pane, .pill-content > .pill-pane {
  display: block;
  height: 0;
  overflow: hidden; }
.tab-content > .active, .pill-content > .active {
  height: auto; }

.main-tab {
  &__tab {
    margin: 0 0 30px 0;
    padding: 0;
    border-bottom: 1px solid #dddddd;
    font-family: $font-roboto-bold;
    text-transform: uppercase;
    font-size: rem(20);
    @include clearfix;
    &>li {
      list-style: none;
      margin: 0 50px 0 0;
      float: left;
      &:last-child {
        margin-right: 0; }
      &>a {
        position: relative;
        display: block;
        height: 43px;
        line-height: 43px;
        color: #747474;
        text-decoration: none;
        &::after {
          content: '';
          left: 0;
          right: 0;
          bottom: -1px;
          height: 3px;
          background: transparent;
          position: absolute; }
        &:hover {
          text-decoration: none;
          color: $color-1; } }
      &.active {
        &>a {
          color: $color-1;
          &::after {
            background: $color-1; } } } } }
  &__block {
    font-size: rem(14);
    margin-bottom: 30px;
    &-date {
      color: $color-1;
      text-transform: uppercase;
      font-size: rem(10);
      font-family: $font-roboto-bold;
      margin-bottom: 3px; }
    p {
      margin-bottom: 0;
      line-height: 22px; } } }

.breadcrumbs {
  margin: -30px 0 10px 0;
  padding: 0;
  font-size: rem(11);
  font-family: $font-roboto;
  color: $color-2;
  @include clearfix;
  &>li {
    list-style: none;
    margin: 0 0 5px 0;
    display: inline-block;
    &+li {
      &::before {
        content: "\f125";
        display: inline-block;
        font-family: $font-ionicons;
        font-size: rem(10);
        color: #b1b1b1;
        margin-left: 5px;
        margin-right: 7px; } }
    &>a, &>span {
      display: inline-block; }
    &>a {
      color: $color-1; } } }

.document {
  &__navigation {
    background: $color-white;
    border-radius: 4px;
    overflow: hidden;
    margin-bottom: 30px;
    padding: 4px 0;
    font-size: rem(14);
    font-family: $font-roboto;
    ul {
      margin: 0;
      padding: 0;
      &>li {
        list-style: none;
        margin: -1px 0 0 0;
        &:first-child {
          margin-top: 0; }
        &>a, &>span {
          display: block;
          border-bottom: 1px solid #e5e5e5;
          color: $color-2;
          min-height: 37px;
          padding: 8px 10px 7px 26px;
          &:hover, &:focus {
            border-bottom-color: $color-1;
            color: $color-white;
            background: $color-1;
            text-decoration: none; } }
        &.active {
          &>a, &>span {
            border-bottom-color: $color-1;
            color: $color-white;
            background: $color-1;
            text-decoration: none; } }
        &:last-child {
          &>a, &>span {
            border-bottom: 0 !important; } } } } } }

.banner {
  margin-bottom: 30px;
  img {
    max-width: 100%;
    height: auto;
    display: block; } }

.mfp-bg {
  opacity: 1;
  background: rgba($color-2,.3); }
.mfp-img-gallery {
  background: rgba($color-2,.7);
  opacity: 1; }
.popup {
  position: relative;
  background: $color-white;
  padding: 20px 20px 27px 20px;
  width: auto;
  max-width: 290px;
  margin: 20px auto;
  border-radius: 4px;
  // overflow: hidden
  box-shadow: 0 2px 20px rgba($color-2,.2);
  color: #444444;
  .btn {
    margin-bottom: 18px; }
  &__close {
    cursor: pointer;
    position: absolute;
    right: 10px;
    top: 10px;
    color: #bebebe;
    font-size: rem(18);
    line-height: 1;
    padding: 5px;
    transition: all 0.35s;
    &:hover {
      color: $color-2; } }
  &__title {
    color: $color-1;
    font-size: rem(18);
    font-family: $font-baron;
    line-height: 20px;
    margin-bottom: 25px;
    &--f16 {
      font-size: rem(16); }
    .small {
      display: block;
      color: #858585;
      font-size: rem(12);
      // text-transform: uppercase
      font-family: $font-roboto;
      margin-top: 5px; } }
  &__form-bottom {
    background: #f3f4f7;
    text-align: center;
    font-size: rem(13);
    height: 70px;
    margin: 23px -20px -27px -20px;
    border-radius: 0 0 4px 4px;
    padding-top: 18px;
    strong {
      font-size: rem(13);
      // font-family: $font-roboto
      display: block;
      line-height: 1;
      margin-bottom: 5px;
      font-family: $font-roboto; }
    .link {
      text-transform: uppercase;
      font-family: $font-roboto-bold;
      &:hover {
        text-decoration: underline; } } }
  &__social {
    &-head {
      display: table;
      width: 100%;
      margin-bottom: 10px;
      font-size: rem(12);
      &>* {
        display: table-cell;
        vertical-align: middle; }
      span {
        padding: 0 5px;
        white-space: nowrap;
        color: $color-2;
        width: 1px; }
      &::after, &::before {
        display: table-cell;
        content: '';
        background-image: linear-gradient(#e5e5e5, #e5e5e5);
        background-size: 100% 1px;
        background-repeat: no-repeat;
        background-position: center; } }
    &-body {
      text-align: center;
      p {
        font-size: rem(12);
        margin-bottom: 12px; }
      a {
        display: inline-block;
        margin: 0 5px;
        opacity: 1;
        width: 30px;
        height: 30px;
        border-radius: 4px;
        position: relative;
        overflow: hidden;
        // background-color: #36729b
        transition: all 0.35s;
        vertical-align: middle;
        svg {
          position: absolute;
          display: block;
          left: 50%;
          top: 50%;
          transform: translate(-50%,-50%); }
        .path--color {
          fill: $color-white; }
        &:hover {
          opacity: 0.7; } } } }
  &__info {
    font-size: rem(12);
    p {
      line-height: 15px;
      margin-bottom: 10px; } }
  &--big {
    max-width: 415px; }
  &__bottom {
    font-size: rem(13);
    background: #f3f4f7;
    margin: 23px -20px -27px -20px;
    padding: 20px;
    border-radius: 0 0 4px 4px;
    p {
      line-height: 20px;
      &:last-child {
        margin-bottom: 0; } } }
  &__gallery {
    &>a {
      position: relative;
      overflow: hidden;
      height: 240px;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-top: -20px;
      margin-bottom: 0px;
      &::after {
        content: '';
        position: absolute;
        right: 0px;
        bottom: 0px;
        width: 25px;
        height: 26px;
        background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 25 26.000001" width="25" height="26"><path d="M10.295 19.636c5.134 0 9.313-4.185 9.313-9.326 0-5.144-4.179-9.327-9.313-9.327C5.158.983.982 5.166.982 10.31c0 5.141 4.176 9.326 9.313 9.326zM6.862 9.818h2.942V6.872a.491.491 0 0 1 .982 0v2.946h2.938a.492.492 0 0 1 0 .983h-2.938v2.944a.492.492 0 0 1-.982 0V10.8H6.862a.492.492 0 0 1 0-.983z" fill="none"/><path d="M6.862 10.801h2.942v2.944a.491.491 0 0 0 .982 0V10.8h2.938a.492.492 0 0 0 0-.983h-2.938V6.872a.492.492 0 0 0-.982 0v2.946H6.862a.492.492 0 0 0 0 .983z" fill="#c2c2c2"/><path d="M10.295 20.62c2.491 0 4.777-.895 6.56-2.374l7.3 7.606a.494.494 0 0 0 .694.012.492.492 0 0 0 .015-.693l-7.284-7.587a10.287 10.287 0 0 0 3.01-7.274C20.59 4.624 15.97 0 10.295 0 4.617 0 0 4.624 0 10.31c0 5.683 4.617 10.31 10.295 10.31zm0-19.637c5.134 0 9.313 4.183 9.313 9.327 0 5.141-4.179 9.326-9.313 9.326-5.137 0-9.313-4.185-9.313-9.326 0-5.144 4.176-9.327 9.313-9.327z" fill="#c2c2c2"/></svg>'); } } }
  &__th {
    width: 100%;
    font-size: rem(12);
    font-family: $font-roboto;
    margin-bottom: 10px;
    th {
      font-size: rem(15);
      font-family: $font-roboto-medium;
      color: $color-1;
      font-weight: 400;
      padding-bottom: 10px; }
    td {
      vertical-align: top;
      padding-bottom: 10px;
      &.last {
        width: 1px;
        white-space: nowrap;
        font-family: $font-roboto-bold;
        vertical-align: bottom; }
      &.first {
        &>span {
          // border-bottom: 1px dotted #aeaeae
          background: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAoAAAABCAIAAABol6gpAAAAGXRFWHRTb2Z0d2FyZQBBZG9iZSBJbWFnZVJlYWR5ccllPAAAABxJREFUeNpiWLdu3f///x88eABkPHz4EI0NEGAAhSwZnIG1SuIAAAAASUVORK5CYII=') repeat-x left calc(100% - 3px);
          min-height: 14px;
          display: block;
          &>span {
            background: $color-white;
            // background: #f3f4f7
            display: inline-block;
            max-width: 90%; } } } }
    tr.separator {
      td {
        padding-bottom: 5px; } } }
  &__img {
    margin-bottom: 10px;
    img {
      margin: 0 auto;
      display: block;
      max-width: 100%;
      height: auto; } } }

.selectric {
  &-wrapper {
    position: relative;
    cursor: pointer; }
  &-responsive {
    width: 100%; }
  border: 0;
  border-bottom: 1px solid #bebebe;
  background: transparent;
  position: relative;
  border-radius: 0px;
  .label {
    display: block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin: 0 20px 0 0;
    font-size: rem(12);
    line-height: 28px;
    color: #444;
    height: 28px;
    font-family: $font-roboto-bold;
    text-transform: uppercase; }
  .button {
    background: none;
    border-radius: 0px;
    display: block;
    position: absolute;
    right: 0px;
    top: 0;
    bottom: 0;
    margin: auto;
    width: 30px;
    height: 30px;
    color: #444;
    text-align: center;
    font: 0/0 a;
    &:after {
      content: "\f35e";
      font-family: $font-ionicons;
      position: absolute;
      top: 0;
      right: 12px;
      bottom: 0;
      left: inherit;
      margin: auto;
      width: 0;
      height: 0;
      border: 0;
      color: #444444;
      font-size: rem(14); } }
  &-focus .selectric {
    border-color: #AAA; }
  &-hover .selectric {
    border-color: #c4c4c4;
    .button {
      color: #c4c4c4;
      &:after {
        border-top-color: #c4c4c4; } } }
  &-open {
    z-index: 9999;
    .selectric {}
    .selectric-items {
      display: block; } }
  &-disabled {
    filter: alpha(opacity = 50);
    opacity: 0.5;
    cursor: default;
    user-select: none; }
  &-hide-select {
    position: relative;
    overflow: hidden;
    width: 0;
    height: 0;
    select {
      position: absolute;
      left: -100%; }
    &.selectric-is-native {
      position: absolute;
      width: 100%;
      height: 100%;
      z-index: 10;
      select {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        height: 100%;
        width: 100%;
        border: none;
        z-index: 1;
        box-sizing: border-box;
        opacity: 0; } } }
  &-input {
    position: absolute !important;
    top: 0 !important;
    left: 0 !important;
    overflow: hidden !important;
    clip: rect(0, 0, 0, 0) !important;
    margin: 0 !important;
    padding: 0 !important;
    width: 1px !important;
    height: 1px !important;
    outline: none !important;
    border: none !important;
    *font: 0/0 a !important;
    background: none !important; }
  &-temp-show {
    position: absolute !important;
    visibility: hidden !important;
    display: block !important; }
  &-items {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    background: #F8F8F8;
    border: 1px solid #C4C4C4;
    z-index: -1;
    box-shadow: 0 0 10px -6px;
    .selectric-scroll {
      height: 100%;
      overflow: auto; } }
  &-above .selectric-items {
    top: auto;
    bottom: 100%; }
  &-items {
    ul {
      list-style: none;
      padding: 0;
      margin: 0;
      font-size: 12px;
      line-height: 20px;
      min-height: 20px; }
    li {
      list-style: none;
      padding: 0;
      margin: 0;
      font-size: 12px;
      line-height: 20px;
      min-height: 20px;
      display: block;
      padding: 8px;
      border-top: 1px solid #FFF;
      border-bottom: 1px solid #EEE;
      color: #666;
      cursor: pointer;
      &.selected {
        background: #EFEFEF;
        color: #444; }
      &.highlighted {
        background: #D0D0D0;
        color: #444; }
      &:hover {
        background: #F0F0F0;
        color: #444; } }
    .disabled {
      filter: alpha(opacity = 50);
      opacity: 0.5;
      cursor: default !important;
      background: none !important;
      color: #666 !important;
      user-select: none;
      display: none; }
    .selectric-group {
      .selectric-group-label {
        font-weight: bold;
        padding-left: 10px;
        cursor: default;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        background: none;
        color: #444; }
      &.disabled li {
        filter: alpha(opacity = 100);
        opacity: 1; }
      li {
        padding-left: 25px; } } }
  &-selectric {
    &--map {
      max-width: 200px;
      .label {
        font-size: rem(26);
        font-family: $font-baron;
        text-transform: lowercase; } } } }

.table-notification {
  width: 100%;
  font-size: rem(12);
  margin-bottom: 10px;
  td {
    vertical-align: top;
    padding-bottom: 12px;
    &+td {
      padding-left: 10px;
      &:last-child {
        padding-left: 17px; } }
    span.span {
      display: inline-block;
      min-height: 12px;
      padding-top: 0; } }
  th {
    font-weight: 400;
    font-size: rem(10);
    position: relative;
    span {
      display: block;
      transform: rotate(-90deg);
      transform-origin: 50%;
      position: absolute;
      right: -9px;
      height: 12px;
      line-height: 12px;
      text-transform: uppercase;
      bottom: 17px;
      width: 31px;
      text-align: left; } } }

.text-uppercase {
  text-transform: uppercase !important; }

.news {
  &__row {
    font-size: rem(13);
    color: $color-2;
    transition: color 0.35s;
    border-bottom: 1px solid #e5e5e5;
    margin-bottom: 20px;
    padding-bottom: 20px;
    &:hover {
      color: #878787; }
    &-title {
      a {
        font-size: rem(15);
        font-family: $font-roboto-bold;
        color: $color-2;
        text-decoration: none;
        display: inline-block;
        line-height: 24px;
        margin-bottom: 5px;
        &:hover {
          color: $color-1;
          text-decoration: none; } } }
    &-date {
      background: $color-1;
      color: $color-white;
      font-size: rem(10);
      white-space: nowrap;
      height: 18px;
      line-height: 18px;
      padding: 0 10px;
      margin-bottom: 10px;
      display: inline-block; }
    p {
      line-height: 22px;
      &:last-child {
        margin-bottom: 0; } } } }

.faq {
  &__row {
    margin-bottom: 10px;
    &:last-child {
      margin-bottom: 0; } }
  &__title {
    font-size: rem(15);
    line-height: 1.14;
    margin-bottom: 10px;
    font-family: $font-roboto-bold; }
  &__block {
    margin-bottom: 2px;
    border: 1px solid #f3f4f7;
    background: #f3f4f7;
    font-size: rem(13);
    transition: background 0.35s;
    &.is-active {
      background: $color-white;
      .faq__block-title {
        color: $color-1;
        font-family: $font-roboto-bold;
        i {
          transform: rotate(-180deg); } } }
    p {
      line-height: 22px;
      margin-bottom: 10px;
