    &.is-active {
      margin-bottom: 10px; }
    &-answer {
      padding: 5px 40px 10px 20px;
      display: none; }
    &-title {
      padding: 8px 40px 7px 20px;
      cursor: pointer;
      position: relative;
      min-height: 35px;
