        font-size: rem(14);
        transform: rotate(0);
        transition: transform 0.35s;
        display: inline-block;
        position: absolute;
        right: 15px;
        top: 12px;
        color: $color-1;
        line-height: 1;
        transform-origin: 50%; } } } }

.table__prices {
  width: 100%;
  font-size: rem(14);
  margin-bottom: 20px;
  &>tbody {
    &>tr {
      &>td {
        padding: 8px;
        line-height: 1.42857143;
        vertical-align: top;
        border-bottom: 2px solid $color-white;
        background: #f3f4f7;
        &:first-child {
          padding-left: 25px; } } } } }

.search {
  margin-bottom: 30px;
  &__form {
    position: relative;
    @include clearfix; }
  &__input {
    width: 100%;
    border: 0;
    height: 30px;
    padding: 0 30px 0 0;
    border-bottom: 1px solid $color-1;
    background: none;
    color: $color-1;
    font-family: $font-roboto-bold;
    font-size: rem(15); }
  &__btn {
    background: none;
    float: right;
    border: 0;
    height: 30px;
    width: 30px;
    position: relative;
    color: #bcbcbc;
    font-size: rem(20);
